@import url(https://fonts.googleapis.com/css2?family=UnifrakturMaguntia&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.leftContainerInitial {
  background-color: #ffffff;
  width: 50vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.textLogoContainer h1 {
  display: flex;
  align-items: center;
  font-family: "Frank Ruhl Libre";
  font-size: 56px;
  font-weight: 700;
  line-height: 0%;
}

.rightContainerInitial {
  background-color: #eeeeee;
  width: 50vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navBarIcon {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 20px 40px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background-color: #e1e1e1;
  border-radius: 5px;
  padding: 10px;
}

.navBarIconContainer {
  display: flex;
  flex-direction: column;
  padding: 15px;
  border-radius: 5px;
}

.textNavBarIcon {
  font-family: "Titillium Web";
  font-size: 16px;
  font-weight: 400;
  text-decoration: none;
  color: #000000;
  margin-bottom: 5px;
}
.textNavBarIcon:hover {
  width: 120%;
  color: #444444;
  opacity: 0.5;
}

.navBar {
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 20px 0px;
  width: 40vw;
}

.textNavBar {
  font-family: "Titillium Web";
  font-size: 18px;
  font-weight: 400;
  text-decoration: none;
  color: #000000;
  width: 100%;
  transition: width 0.8s;
  margin-bottom: 10px;
}
.textNavBar:hover {
  width: 120%;
  color: #444444;
  opacity: 0.5;
}
.textNavBar:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  bottom: 0;
  left: 0;
  background-color: #dddddd;
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.3s ease-in-out 0.2s;
  transition-delay: 0.1s;
}
.textNavBar:hover:before {
  visibility: visible;
  transform: scaleX(1);
}

.logoContainer {
  position: absolute;
}

.arrow {
  position: absolute;
  bottom: 0;
  margin-bottom: 20px;
  padding: 20px;
}

.arrow a {
  color: #000000;
}

.bounce {
  animation: bounce 2.5s infinite;
}
@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}

@media screen and (max-width: 825px) {
  .leftContainerInitial {
    width: 50vw;
  }

  .rightContainerInitial {
    width: 50vw;
  }

  .textLogoContainer h1 {
    display: flex;
    align-items: center;
    font-family: "Frank Ruhl Libre";
    font-size: 8vw;
    font-weight: 700;
    line-height: 0%;
  }

  .navBar {
    display: none;
  }
}

@media screen and (min-width: 825px) {
  .navBarIcon {
    display: none;
  }
}

